import React from "react"
import './ChatBox.css'
import lodash from 'lodash'
import { socket } from "../utils/Socket"
import Login from "./Login"
import { msgListRef, loginRef } from "../utils/Socket"


class PeerList extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            peers: []
        }
    }
    setPeerList(peers){
        this.setState({
            peers: peers
        })
    }
    render(){
        const peers = this.state.peers
        return (<div className="peers">
            <div className="peer-list">
                {peers.map(e => <p>{e}</p>)}
            </div>
            <button>refresh peers</button>
        </div>)
    }
}

class MsgBubble extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            type: props.type,
            data: props.data,
            tplCnt: 0
        }
    }
    componentDidMount(){
        if(this.state.type === 'tpl'){
            this.setState({
                tplCnt: this.state.tplCnt + 1
            })
        }
    }
    templateCompile(tpl, ctx){
        let attrs = {}
        Array.from(tpl.matchAll(/{{\s*(\w+)\s*}}/g)).forEach(a => {
            attrs[a[1]] = 'unknown'
        })
        lodash.merge(attrs, JSON.parse(ctx))
        console.log(attrs)
        for(let k in attrs){
            tpl = tpl.replaceAll(`{{${k}}}`, attrs[k])
        }
        let out = <iframe title={this.state.tplCnt} sandbox="" srcDoc={tpl}> wow </iframe>
        return out
    }
    render(){
        const data = this.state.data
        const type = this.state.type
        if(type === 'text'){
            return <p className="text">{data}</p>
        }
        if(type === 'link'){
            return <a className="text" href={data}>click this</a>
        }
        if(type === 'tpl'){
            return this.templateCompile(data.tpl, data.ctx)
        }
        if(type === 'image'){
            // console.log(this.props)
            // if(data.attrs)
            if(this.props.allowImage){
                // return <img src={data.src} {...data.attrs} ></img>
                return <div style={{
                    backgroundImage: `url(${data.src})`,
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat",
                    // width: '100%',
                    padding: '25%',
                    height: 0,
                }} {...data.attrs}/>
            }else{
                return <p className="warning-text">sorry, <code>allowImage</code> is false</p>
            }
        }
        return <div />
    }
}

class MsgList extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            msgs: []
        }
    }
    setMsgList(msgs){
        this.setState({
            msgs: msgs
        })
    }
    
    render(){
        const msgs = this.state.msgs
        const msgDivs = msgs.map((m, i) => <div className="msg-item" key={i}>
            <div className="msg-card">
                <p className="from">{m.from}</p>
                <div className="content">
                    <MsgBubble type={m.content.type} data={m.content.data} />
                </div>
            </div>
        </div>)
        return (<div className="msg-list">
            {msgDivs}
        </div>)
    }
}

class SendBox extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            msgType: null
        }
    }
    selectOnChange(event){
        event.preventDefault()
        // console.log(event.target.value)
        this.setState({
            msgType: event.target.value
        })
    }
    sendMsg(event){
        console.log('sendmsg')
        event.preventDefault()
        const to = event.target.to.value
        const data = event.target.data.value
        const type = event.target.type.value
        const tpl = event.target.tplid && event.target.tplid.value + '.tpl'
        // console.log(to, type, data, tpl)
        // return
        socket.sendMsg(to, {
            type: type,
            data: data ? type !== 'tpl' : {
                tpl: tpl || 'test.tpl',
                ctx: data
            }
        })
    }
    render(){
        return (<>
            <form onSubmit={e => {this.sendMsg(e)}}>
                <label>message type: </label>
                <select name="type" id="pet-select" onChange={e => this.selectOnChange(e)}>
                    {/* <option value="">--Please choose an type--</option> */}
                    <option value="text">Text</option>
                    <option value="link">link</option>
                    <option value="tpl">template</option>
                    <option value="image">image</option>
                </select>
                <p><label>to: </label><input name="to" /></p>
                {this.state.msgType === 'tpl' 
                && <p><label>template id: </label><input name="tplid" /></p>}
                <textarea name="data"></textarea>
                <p><button type="submit">send</button></p>
            </form>
            <a href="/tpl" target="_blank">
                use templates to make your message more beautiful!
            </a>
        </>)
    }
}

class ChatBox extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            loggedin: false
        }
    }
    setLoginState(state){
        this.setState({
            loggedin: state
        })
    }
    render(){
        return (<> 
            {
                ! this.state.loggedin
                ? <Login/> 
                : <>
                    {false && <PeerList />}
                    <SendBox />
                    <MsgList ref={msgListRef}/>
                </>
            }
        </>)
    }
}

class ChatPage extends React.Component{
    render(){
        return <ChatBox ref={loginRef}/>
    }
}

export default ChatPage