import React from "react";
import { isJson } from '../utils/util'

class TplPage extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            tpls: []
        }
    }

    handleSubmit(event){
        event.preventDefault()
        const tpl = event.target.content.value
        fetch('/tplCreate', {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                tpl: tpl
            })
        })
        .then(res => { alert(res.text()) })
        .catch(err => { alert(err) })
    }

    getTpls(){
        fetch('/tplList', {
            method: 'get'
        })
        .then(res => res.json())
        .then(res => {
            console.log(res)
            if(isJson(res)){
                this.setState({
                    tpls: JSON.parse(res)
                })
            }else{
                alert('something error')
            }
        })
        .catch(err => { 
            alert(err) 
        })
    }
    render(){
        return (<>
            <h3>Create a template here</h3>
            <form onSubmit={e => { this.handleSubmit(e) }}>
                <textarea name="content"></textarea>
                <p><button type="submit">submit</button></p>
            </form>
            <h3>Existing templates</h3>
            <div>
                {this.state.tpls.map(e => <p><a href={"/tplView?tpl="+e}>{e}</a></p>)}
            </div>
            <p>
                <button onClick={this.getTpls}>refresh</button>
            </p>
        </>)
    }
}

export default TplPage