import React from "react"
import { isJson } from "./util.js"

const WS_URL = 'ws://localhost:8081'

const msgListRef = React.createRef()
const loginRef = React.createRef()

class Socket{
    constructor(){
        this.socket = null
        this.username = null
        this.peers = []
        this.msgs = []
    }

    init(username){
        console.log("connecting...")
        this.username = username
        this.socket = new WebSocket(WS_URL)
        
        this.socket.onopen = () => {
            console.log('connected')
            const msg = {
                api: "ping",
                data: "hello world"
            }
            this.socket.send(JSON.stringify(msg))
            // this.socket.send(JSON.stringify({api: "list"}))
        }

        this.socket.onmessage = (e) => {
            const msg = e.data
            // console.log(msg)
            if(isJson(msg)){
                this.handleMsg(JSON.parse(msg))
            }
        }

        this.socket.onclose = () => {
            this.username = null
            console.log("closed")
            loginRef.current.setLoginState(false)
        }
    }

    sendMsg(to, msg){
        if(!this.username){
            loginRef.current.setLoginState(false)
            return
        }
        this.socket.send(JSON.stringify({
            api: 'sendmsg',
            to: to,
            msg: msg
        }))
    }

    handleMsgRecv(from, content){
        console.log('[message received] from', from, content)
        this.msgs.push({ from:from, content:content })
        msgListRef.current.setMsgList(this.msgs)
    }

    handleFlag(flag){
        console.log(flag)
    }

    handleWarning(warning){
        alert(warning)
    }

    handleMsg(msg){
        switch(msg.api){
            case "login":
                this.handleLogin()
                break
            case "list":
                this.handleList(msg.peers)
                break
            case "message":
                this.handleMsgRecv(msg.from, msg.content)
                break
            case "flag":
                this.handleFlag(msg.flag)
                break
            case "warning":
                this.handleWarning(msg.warning)
                break
            default:
                console.log("unknown api", msg.api)
        }
    }

    handleLogin(){
        this.socket.send(JSON.stringify({
            api: "login",
            username: this.username 
        }))
        this.socket.send(JSON.stringify({ api: "list" }))
        loginRef.current.setLoginState(true)
    }

    handleList(peers){
        this.peers = peers
    }

    refreshPeers(){
        this.socket.send(JSON.stringify({ api: "list" }))
    }
}

const socket = new Socket()

export default Socket;
export { socket, msgListRef, loginRef }